'use client';
import React, { useEffect, useState } from 'react';
import Link from 'next/link';
import ImageLoader from './ImageLoader'; // Adjust the path as necessary
import Logo from "../public/logo.svg"; // Adjust the path accordingly
import Image from "next/image"; // Add StaticImageData import

export default function Navbar() {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      // 當捲動超過 50 像素時，設定背景變為深藍色
      if (window.scrollY > 50) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <nav
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        padding: '1rem 2rem',
        backgroundColor: scrolled ? '#001f3f' : 'transparent',
        transition: 'background-color 0.3s ease',
        zIndex: 1000,
      }}
    >
      <div
        style={{
          maxWidth: '1200px',
          margin: '0 auto',
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
        }}
      >
        <div style={{ 
          fontSize: '1.5rem', 
          fontWeight: 'bold',
          backgroundImage: `url(${ImageLoader({ src: '/logo.svg' })})`,
          color: '#fff' }}>
          {/* Logo1 */}
          <Logo fill="#fff" width={240} height={24} />
          {/* <Image src={Logo} alt="Newspaper Icon" width={240} height={24} className="mr-2" /> */}
        </div>
        <ul
          style={{
            display: 'flex',
            gap: '1rem',
            listStyle: 'none',
            margin: 0,
            padding: 0,
          }}
        >
          <li>
            <Link href="/about">
              <span style={{ color: '#fff', textDecoration: 'none', cursor: 'pointer' }}>
                關於我們
              </span>
            </Link>
          </li>
          <li>
            <Link href="/services">
              <span style={{ color: '#fff', textDecoration: 'none', cursor: 'pointer' }}>
                服務項目
              </span>
            </Link>
          </li>
          <li>
            <Link href="/contact">
              <span style={{ color: '#fff', textDecoration: 'none', cursor: 'pointer' }}>
                聯絡我們
              </span>
            </Link>
          </li>
        </ul>
      </div>
    </nav>
  );
}